import interfaceurl from '@/utils/interface.js'
//引入store的内容
import {
	mapState,
	mapMutations,
	mapActions
} from 'vuex'
export default {
	computed: mapState(['userInfo']),
	data() {
		return {
			collectionList: [], //收藏列表
		}
	},
	onLoad() {
		this.getCollection();
	},
	methods: {
		getCollection() {
			let that = this;
			//that.userInfo.userId = 97;
			interfaceurl.checkAuth(interfaceurl.collection, {userId: that.userInfo.userId}).then((res) => {
				that.collectionList = res.data;
			});
		},
		delCollect(id) {
			let ids = [];
			if (id === 0) { //移除全部
				for (var item of this.collectionList) {
					ids.push(item.id);
				}
			} else {
				ids.push(id);
			}
			let that = this;
			//that.userInfo.userId = 97;
			interfaceurl.checkAuth(interfaceurl.delCollection, ids).then((res) => {
				that.getCollection();
			});
		}
	}
}
